@import url(https://fonts.googleapis.com/css?family=GFS+Didot|Montserrat:300,600);
.App {
  font-family: 'Montserrat', sans-serif;
  color: #080813;
  margin: 0 10%; }

@media (max-width: 1440px) {
  .App {
    margin: 0 5%; } }

@media (max-width: 768px) {
  .App {
    margin: 0; } }

ul {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row;
          flex-flow: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 0;
  padding: 1%;
  font-size: 1.3rem; }
  ul .brand {
    font-weight: 600; }
  ul .navigation {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row;
            flex-flow: row;
    margin: 0 -15px; }
    ul .navigation li {
      padding: 0 15px; }
  ul li {
    list-style-type: none; }
    ul li a {
      color: inherit;
      text-decoration: none; }
    ul li a:hover {
      font-weight: 600; }

@media (max-width: 768px) {
  ul {
    font-size: 1rem; } }

@media (max-width: 425px) {
  ul {
    font-size: 1.3rem;
    -webkit-flex-flow: column;
            flex-flow: column; }
    ul .navigation {
      margin: 5px -15px; } }

@media (max-width: 320px) {
  ul {
    font-size: 1.1rem;
    -webkit-flex-flow: column;
            flex-flow: column; }
    ul .navigation {
      margin: 5px -15px; } }

section {
  margin: 7% 0 0 0;
  padding: 0 1%; }
  section p {
    margin: 0;
    font-size: 1.7rem;
    width: 80%; }
  section .action-call {
    font-size: 7.8rem; }
  section .button-group {
    margin: 7% 0 0 0; }
    section .button-group a {
      display: inline-block;
      font-family: inherit;
      font-size: 1.7rem;
      font-weight: 300;
      text-align: center;
      line-height: 50px;
      text-decoration: none;
      width: 251px;
      height: 50px;
      cursor: pointer;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25); }
    section .button-group .work-button {
      background-color: #080813;
      border: 1px solid #080813;
      color: #FFF;
      transition: .2s; }
    section .button-group .work-button:hover {
      width: 300px; }
    section .button-group .blog-button {
      background-color: #FFF;
      color: #080813;
      border: 1px solid #080813;
      margin: 0 1.5%;
      transition: .2s; }
    section .button-group .blog-button:hover {
      width: 300px; }

@media (max-width: 768px) {
  section {
    margin: 15% 0 0 0; }
    section .button-group {
      margin: 15% 0 0 0; }
      section .button-group .blog-button {
        margin: 0 4.5%; } }

@media (max-width: 425px) {
  section p {
    width: 100%; }
  section .button-group {
    margin: 30% 0 0 0; }
    section .button-group .work-button {
      width: 100%; }
    section .button-group .blog-button {
      width: 100%;
      margin: 5% 0; }
    section .button-group .work-button::hover {
      width: 100%; }
    section .button-group .blog-button:hover {
      width: 100%; } }

.contact-page {
  margin: 7% 0 0 0; }
  .contact-page h1 {
    font-weight: 300;
    font-size: 3.9rem;
    margin: 0; }
  .contact-page h2 {
    font-weight: 300;
    font-size: 1.5rem;
    margin: 0; }

.contact-form {
  margin-top: 5%;
  width: 75%; }
  .contact-form form {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: column;
            flex-flow: column; }
    .contact-form form label, .contact-form form input, .contact-form form textarea {
      font-family: inherit;
      display: block;
      border: none;
      font-size: 1.2rem;
      width: 50%;
      outline: none; }
    .contact-form form label {
      font-size: 1rem; }
    .contact-form form input {
      margin: 1% 0 3% 0;
      border: 1px dashed #080813;
      padding: 5px; }
    .contact-form form textarea {
      border: 1px dashed #080813;
      resize: none;
      width: 100%;
      height: calc(1.2rem * 6);
      margin: 2% 0 8% 0; }
    .contact-form form button {
      display: inline-block;
      font-family: inherit;
      font-size: 1.7rem;
      text-align: center;
      line-height: 50px;
      width: 251px;
      height: 50px;
      cursor: pointer;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      background-color: #080813;
      border: 1px solid #080813;
      color: #FFF;
      transition: .2s; }
    .contact-form form button:hover {
      width: 300px; }
    .contact-form form button:active {
      background-color: #fff;
      color: #080813; }
  .contact-form footer {
    margin-top: 18px; }

